import React, { useState, useEffect } from "react";
import feathersClient from "feathersClient";
import styled, { css } from "styled-components";

type Props = {
    readonly active?: boolean;
};

const Container = styled.div`
    position: relative;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr auto;
    transition: 0.15s ease-in;

    &:not(:first-child)::after {
        content: "";
        position: absolute;
        top: 0;
        z-index: 0;
        width: 100%;
        border-top: var(--border-variant);
    }
`;

const Box = styled.div`
    display: grid;
    grid-template-columns: auto 1fr;
    align-items: center;
    justify-content: start;

    &:hover {
        cursor: pointer;
        background: var(--primary-variant);
        transition: 0.15s ease-out;
    }

    div:first-child {
        position: relative;
        padding: 1rem 2rem;
        text-align: center;
        text-transform: uppercase;

        h6,
        p {
            font-family: var(--font-family-alt);
        }

        &::after {
            content: "";
            position: absolute;
            top: calc(50% - 1rem);
            right: 0;
            height: 2rem;
            border-right: var(--border-variant);
        }
    }

    div:last-child {
        padding: 1.5rem 2.5rem;
        display: grid;
        grid-auto-flow: column;
        align-items: center;
        justify-content: start;
        gap: 1rem;
    }

    h4 {
        font: 400 1rem/1.75rem var(--font-family-alt);
    }
`;

const Cliente = styled.h5<Props>`
    position: relative;
    padding: 1.5rem 2.5rem;
    border: 1px solid rgba(0, 0, 0, 0);
    display: grid;
    transition: 0.15s ease-in;

    &:hover {
        cursor: pointer;
        background: var(--primary-variant);
        transition: 0.15s ease-out;
    }

    &::after {
        content: "";
        position: absolute;
        top: calc(50% - 1rem);
        left: 0;
        height: 2rem;
        border-left: var(--border-variant);
    }

    ${(props) =>
        props.active &&
        css`
            background: var(--primary-variant);
            transition: 0.15s ease-out;
        `};
`;

const Vehiculo = function ({
    vehiculo,
    active,
    setClienteId,
    setVehiculoId,
    matchModelo,
}) {
    const [cliente, setCliente] = useState({
        id: 0,
        nombre: "",
        apellido: "",
        telefono: " ",
        direccion: "",
        empresa: "",
        createdAt: "",
        updatedAt: "",
    });

    useEffect(() => {
        feathersClient
            .service("clientes")
            .get(vehiculo.clienteId)
            .then((found) => {
                setCliente(found);
            })
            .catch((error) => {
                console.log("error", error);
            });
    }, [vehiculo]);

    return (
        <Container>
            <Box
                onClick={() => {
                    setVehiculoId(vehiculo.id);
                    setClienteId(vehiculo.clienteId);
                }}
            >
                <div>
                    <p>
                        {vehiculo.updatedAt.substring(8, 10)}
                        <span>
                            {new Date(vehiculo.updatedAt)
                                .toLocaleDateString("default", {
                                    month: "short",
                                })
                                .substring(0, 3)}
                        </span>
                    </p>
                </div>
                <div>
                    <h4>{vehiculo.patente}</h4>
                    <p>{matchModelo(vehiculo.modeloId)}</p>
                </div>
            </Box>
            <Cliente active={active} onClick={() => setClienteId(cliente.id)}>
                {cliente.nombre} {cliente.apellido}
            </Cliente>
        </Container>
    );
};

export default Vehiculo;
